import "./App.css";
import React from "react";

import { AppMenuBar } from "./components/AppMenuBar";
import SearchBox from "./components/SearchBox";
import CopyrightFooter from "./components/CopyrightFooter";
import listings from "./listings.json";
import ListGroup from "./components/ListGroup";
import Container from "@mui/material/Container";
import CssBaseline from "@mui/material/CssBaseline";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { Grid } from "@mui/material";

const theme = createTheme();

export default function App() {
  const state = { search: "", data: listings };
  var filteredData = listings.filter((item) => item.industry.includes("Tech"));
  console.log(filteredData);

  function handleInputChange(event) {
    const value = event.target.value;
    state.search = value;
    state.data = listings.filter((item) => item.industry.includes(value));
  }

  return (
    <div>
      {AppMenuBar}
      <CssBaseline />
      <ThemeProvider theme={theme}>
        <Container component="main" maxWidth="lg">
          <SearchBox handleInputChange={handleInputChange} />
          <Grid container spacing={2}>
            {state.data.map((industry, key) => {
              return (
                <Grid item xs={4} key={key}>
                  <ListGroup industryList={industry} />
                </Grid>
              );
            })}
          </Grid>
          <CopyrightFooter sx={{ mt: 5 }} />
        </Container>
      </ThemeProvider>
    </div>
  );
}

